import React from 'react';
import Slider from "react-slick";

class Carousel extends React.Component {
    render() {
      var settings = {
        dots: true,
        infinite: true,
        speed: 500,
        slidesToShow: 1,
        slidesToScroll: 1,
            infinite: true,
            autoplay: true,
            autoplaySpeed: 5000,
            pauseOnHover: false,
            centerMode: true
      }
      
      return (
        <div className="container sizing">
          <Slider {...settings}>
            <div>
              <img src="https://static.vibe.com/files/2018/01/black-panther-poster-1517325222-640x933.jpg" />
            </div>
            <div>
            <img src="https://static.vibe.com/files/2018/01/black-panther-poster-1517325222-640x933.jpg" />
            </div>
            <div>
              <img src="https://static.vibe.com/files/2018/01/black-panther-poster-1517325222-640x933.jpg" />
            </div>
            <div>
              <img src="https://static.vibe.com/files/2018/01/black-panther-poster-1517325222-640x933.jpg" />
              
            </div>
          </Slider>
          </div>
      );
    }
  }
  
  export default Carousel;


